@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        font-size: 15px;
    }
    
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: theme('fontFamily.serif');
        text-align: left;
        font-weight: 100 !important;
        letter-spacing: -0.03em;
    }

    body {
        font-family: theme('fontFamily.sans');
    }
}

@layer base {
    :root {
        /* Berget theme colors */
        --background: 7 7% 95%;
        --foreground: 240 10% 3.9%;
        --card: 0 14% 100%;
        --card-foreground: 240 10% 3.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 240 10% 3.9%;
        --primary: 240 5.9% 10%;
        --primary-foreground: 0 0% 98%;
        --secondary: 240 4.8% 95.9%;
        --secondary-foreground: 240 5.9% 10%;
        --muted: 240 4.8% 95.9%;
        --muted-foreground: 240 3.8% 46.1%;
        --accent: 240 4.8% 95.9%;
        --accent-foreground: 240 5.9% 10%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 240 5.9% 90%;
        --input: 240 5.9% 90%;
        --ring: 240 5.9% 10%;
        --radius: 1rem;
        --chart-1: 12 76% 61%;
        --chart-2: 173 58% 39%;
        --chart-3: 197 37% 24%;
        --chart-4: 43 74% 66%;
        --chart-5: 27 87% 67%;
    }

    .dark {
        /* Berget dark theme colors */
        --background: 240 5% 10%;
        --foreground: 0 0% 98%;
        --card: 240 10% 5.9%;
        --card-foreground: 0 0% 98%;
        --popover: 240 10% 3.9%;
        --popover-foreground: 0 0% 98%;
        --primary: 0 0% 98%;
        --primary-foreground: 240 5.9% 10%;
        --secondary: 240 3.7% 15.9%;
        --secondary-foreground: 0 0% 98%;
        --muted: 240 3.7% 15.9%;
        --muted-foreground: 240 5% 64.9%;
        --accent: 240 3.7% 15.9%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 240 3.7% 15%;
        --input: 240 3.7% 15%;
        --ring: 240 5% 64.9%;
        --chart-1: 220 70% 50%;
        --chart-2: 160 60% 45%;
        --chart-3: 30 80% 55%;
        --chart-4: 280 65% 60%;
        --chart-5: 340 75% 55%;
    }
}

@layer components {
    .link-style {
        @apply underline underline-offset-4 font-semibold text-lg;
    }
}
@layer components {
    .input-error {
        @apply text-lg mt-2 text-red-500 font-bold py-0.5 px-1;
    }
}

input[type="checkbox"],
input[type="radio"] {
    margin: 0; /* Remove the extra margin */
    vertical-align: middle; /* Vertically align checkbox and radio buttons with text */
    line-height: normal; /* Ensure normal line height */
}
.responsive-container {
    @apply flex flex-col space-y-2 md:flex-row md:space-y-0 md:space-x-2 items-center;
}
